import { httpClient } from '../httpClient';
import { UserStatus } from './types/UserStatus';
import type { CreateNewUserResponse } from './urls';

export async function createNewUser({
  url,
  deviceId,
  deviceIp,
  withSubscriptionStatus,
}: {
  url: string;
  deviceId: string;
  deviceIp?: string;
  withSubscriptionStatus?: UserStatus;
}): Promise<{ userId: string }> {
  const data = await httpClient
    .post<CreateNewUserResponse>(url, {
      json: { deviceId, deviceIp, withSubscriptionStatus },
    })
    .json();
  return data;
}
