import { getCreateNewUserApiUrl } from '../../api/premium/urls';
import {
  getSubscriptionInfo,
  setLocalStoragePremiumData,
} from '../../app/storage/local/premium';
import { createNewUser } from '../../api/premium/createNewUser';
import { getSessionMetaApiData } from '../../app/storage/session/metaApiData';
import { setSessionStorageData } from '../../app/storage/session/shared';
import { getDeviceIP, getDeviceId } from './utils';
import { backgroundScope } from '@background/shared';
import { analytics } from '../analytics/ga';
import { UserStatus } from '@api/premium/types/UserStatus';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/shared';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';
import { Actions } from '@app/actions/shared';

export async function setUserId(userStatus?: UserStatus): Promise<string> {
  try {
    const [deviceId, deviceIp, { apiBaseUrl }] = await Promise.all([
      getDeviceId(),
      getDeviceIP(),
      getSessionMetaApiData(),
    ]);

    const { userId } = await createNewUser({
      url: getCreateNewUserApiUrl(apiBaseUrl),
      deviceId,
      deviceIp,
      withSubscriptionStatus: userStatus,
    });

    await setLocalStoragePremiumData({
      userId,
    });

    backgroundScope.setExtras({
      userId,
      deviceId,
    });

    backgroundScope.setUser({
      id: userId,
    });

    backgroundScope.captureMessage('User ID created', 'info');
    await analytics.trackUserAction('user_created', 'premium');

    const subscription = await getSubscriptionInfo();

    await setLocalStoragePremiumData({
      hasPremium: subscription?.status !== 'New',
    });

    await setLocalStorageData({
      [LocalStorageKey.OnboardingScreenStatus]: OnboardingStep.Done,
    });

    chrome.runtime.sendMessage({
      action: Actions.CreateUserExpiredUserSuccess,
    });

    return userId;
  } catch (error) {
    if (error instanceof Error && error.message === 'Failed to fetch') {
      await setSessionStorageData({
        requestBlockedError: 'request createNewUser is probably blocked',
      });
      await analytics.trackError(error.message, 'user_creation_failed');
    }
    throw error;
  }
}
