import { updatePacScript } from '@background/pac-script/updatePacScript';
import { fetchProxyData } from '../../../api/fetchProxyData';
import { fetchPremiumProxyData } from '../../../api/premium/fetchPremiumProxyData';
import {
  PremiumAlarms,
  getChromePremiumAlarm,
} from '../../../app/alarms/premium';
import { Alarms } from '../../../app/alarms/shared';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
  getSubscriptionInfo,
  setLocalStoragePremiumData,
} from '../../../app/storage/local/premium';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '../../../app/storage/local/shared';
import { getSessionMetaApiData } from '../../../app/storage/session/metaApiData';
import { type ProxyDataResponse } from './types/ProxyData';

export async function getPremiumProxyData({
  apiBaseUrl,
  deviceId,
  deviceIp,
  onFail,
}: {
  apiBaseUrl: string;
  deviceId: string;
  deviceIp?: string;
  onFail?: boolean;
}): Promise<ProxyDataResponse> {
  const { userId, hasPremium } = await getLocalStoragePremiumData([
    LocalStoragePremiumKey.UserId,
    LocalStoragePremiumKey.HasPremium,
  ]);

  if (userId && hasPremium) {
    const data = await fetchPremiumProxyData({
      apiBaseUrl,
      userId,
      deviceId,
      deviceIp,
      onFail,
    });

    if ('error' in data) {
      if (data.error === 'changeDevice') {
        await setLocalStoragePremiumData({ isChangeDeviceError: true });
        await setLocalStorageData({ targetState: 'disconnected' });
      }
      throw new Error(data.error);
    }

    return data;
  }

  const data = await fetchProxyData({ apiBaseUrl, deviceId, deviceIp, onFail });
  return data;
}

async function updatePremiumProxyData(): Promise<void> {
  const { status, hasPremium } = await getLocalStoragePremiumData([
    LocalStoragePremiumKey.Status,
    LocalStoragePremiumKey.HasPremium,
  ]);

  if (hasPremium && status !== 'Paid') {
    await setLocalStorageData({ targetState: 'disconnected' });
    return;
  }

  await chrome.alarms.create(Alarms.EnableProxy, {
    delayInMinutes: 0,
  });
}

chrome.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as PremiumAlarms;

  if (name === PremiumAlarms.UpdateProxyPremiumData) {
    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    if (currentState === 'connected') {
      await updatePremiumProxyData();
    }

    await updatePacScript();
  }

  if (name === PremiumAlarms.UpdateSubscriptionInfo) {
    await getSubscriptionInfo();
  }
});

chrome.storage.session.onChanged.addListener(
  async (changes: { isInitialized?: { newValue: boolean } }) => {
    if (changes.isInitialized?.newValue) {
      await createOrUpdatePremiumProxyDataAlarm();

      const updateSubscriptionInfoAlarm = await getChromePremiumAlarm(
        PremiumAlarms.UpdateSubscriptionInfo,
      );

      const { updateSubscriptionInfoPeriod } = await getSessionMetaApiData();

      if (!updateSubscriptionInfoAlarm) {
        await chrome.alarms.create(PremiumAlarms.UpdateSubscriptionInfo, {
          periodInMinutes: updateSubscriptionInfoPeriod,
          delayInMinutes: updateSubscriptionInfoPeriod,
        });
      }
    }
  },
);

chrome.storage.local.onChanged.addListener(
  async (changes: { hasPremium?: { newValue: boolean } }) => {
    if (changes.hasPremium) {
      await createOrUpdatePremiumProxyDataAlarm();

      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );

      if (currentState === 'connected') {
        await updatePremiumProxyData();
      }
    }
  },
);

async function createOrUpdatePremiumProxyDataAlarm(): Promise<void> {
  const updatePremiumProxyAlarm = await getChromePremiumAlarm(
    PremiumAlarms.UpdateProxyPremiumData,
  );

  if (!updatePremiumProxyAlarm) {
    const { proxyUpdatePeriodPremium, proxyUpdatePeriod } =
      await getSessionMetaApiData();
    const { hasPremium } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.HasPremium,
    );

    const updatePeriod = hasPremium
      ? proxyUpdatePeriodPremium
      : proxyUpdatePeriod;

    await chrome.alarms.create(PremiumAlarms.UpdateProxyPremiumData, {
      periodInMinutes: updatePeriod,
      delayInMinutes: updatePeriod,
    });
  }
}
