import { Tariff } from '@api/premium/types/Tariff';
import { PremiumActions } from '@app/actions/premium';
import { Actions } from '@app/actions/shared';
import { PremiumAlarms } from '@app/alarms/premium';
import { PremiumMessage } from '@app/messages/premium';
import {
  type Message,
  type NavigateToFreeTrialMessage,
  type NavigateToPaymentMessage,
} from '@app/messages/shared';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
  getSubscriptionInfo,
  setLocalStoragePremiumData,
} from '@app/storage/local/premium';
import {
  LocalStorageKey,
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getSessionMetaApiData } from '@app/storage/session/metaApiData';
import {
  SessionStorageKey,
  getSessionStorageData,
  setSessionStorageData,
} from '@app/storage/session/shared';
import { analytics } from '../analytics/ga';
import { getPremiumProxyData } from '../network/proxy/premiumProxyController';
import { initProxy, proxyController } from '../network/proxy/proxyController';
import { redirectToVideo } from '../redirect/utils';
import '../shared';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';
import { setUserId } from '@background/client/premium';
import { UserStatus } from '@api/premium/types/UserStatus';

async function createUserIfNotExistsAndGetSubscription({
  userStatus,
}: {
  userStatus?: UserStatus;
}): Promise<void> {
  try {
    const { userId } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.UserId,
    );
    if (!userId) {
      await setUserId(userStatus);
    }
  } catch (err) {
    console.error(err);
  }
}

async function openExtensionPopup(): Promise<void> {
  try {
    if ('openPopup' in chrome.action) {
      const window = await chrome.windows.getCurrent();
      if (window.id) {
        await chrome.windows.update(window.id, { focused: true });
      }
      await chrome.action.openPopup({ windowId: window.id });
    }
  } catch (err) {
    console.warn(err);
  }
}

async function setUninstallUrl(userId: string) {
  const uninstallUrl = new URL(process.env.ON_DELETE_URL ?? '');
  if (userId) {
    uninstallUrl.searchParams.set('clientId', userId);
  }

  chrome.runtime.setUninstallURL(uninstallUrl.toString(), () => {
    if (chrome.runtime.lastError) {
      console.warn('Failed to set uninstall URL:', chrome.runtime.lastError);
    }
  });
}

chrome.runtime.onMessage.addListener(async (message: Message) => {
  if (message.action === Actions.OpenPopup) {
    await openExtensionPopup();

    chrome.runtime.onMessage.addListener(async function listener(msg: Message) {
      const { userId } = await chrome.storage.local.get(
        LocalStoragePremiumKey.UserId,
      );

      if (msg.action === Actions.PopupReady && userId) {
        await chrome.runtime.sendMessage<NavigateToFreeTrialMessage>({
          action: Actions.NavigateToFreeTrial,
        });
        chrome.runtime.onMessage.removeListener(listener);
      }
    });
  }

  if (message.action === Actions.OpenPopupChangeDevice) {
    // TODO we need to find solution for open popup on change device
  }

  if (message.action === Actions.CreateUserExpiredUser) {
    await createUserIfNotExistsAndGetSubscription({
      userStatus: UserStatus.Expired,
    });
  }
});

chrome.runtime.onMessage.addListener(async (message: Message) => {
  if (message.action === Actions.OpenPopupPayment) {
    await openExtensionPopup();

    chrome.runtime.onMessage.addListener(async function listener(msg: Message) {
      if (msg.action === Actions.PopupReady) {
        await chrome.runtime.sendMessage<NavigateToPaymentMessage>({
          action: Actions.NavigateToPayment,
        });
        chrome.runtime.onMessage.removeListener(listener);
      }
    });
  }
});

async function openExtensionUpdateWelcomePage(
  previousVersion: string,
): Promise<void> {
  try {
    const data = await getSessionMetaApiData();

    const prevVersion = versionStringToNumber(previousVersion);

    const showOnUpdateVersion = versionStringToNumber(
      data.showOnUpdateVersion ?? chrome.runtime.getManifest().version,
    );

    const [{ apiBaseUrl }, { deviceId, deviceIp }, { userId, hasPremium }] =
      await Promise.all([
        getSessionMetaApiData(),
        getLocalStorageData([
          LocalStorageKey.DeviceId,
          LocalStorageKey.DeviceIp,
        ]),
        getLocalStoragePremiumData([
          LocalStoragePremiumKey.UserId,
          LocalStoragePremiumKey.HasPremium,
        ]),
      ]);

    const isCorrectVersion = showOnUpdateVersion > prevVersion;
    const isFreeUser = userId && deviceId && !hasPremium;

    if (process.env.ON_UPDATE_URL && isCorrectVersion && isFreeUser) {
      const { status } = await fetchCheckTariffAvailability(apiBaseUrl, {
        userId,
        deviceId,
        deviceIp,
        tariff: Tariff.FreeTrial,
      });

      if (status === 'allowed') {
        await chrome.tabs.create({ url: process.env.ON_UPDATE_URL });
      }
    }
  } catch (err) {
    console.error(err);
  }
}

chrome.runtime.onInstalled.addListener(async (details) => {
  await chrome.storage.session.clear();

  if (details.reason === chrome.runtime.OnInstalledReason.INSTALL) {
    await analytics.trackUserAction('install', 'extension');
    await openExtensionPopup();
  }

  if (details.reason === chrome.runtime.OnInstalledReason.UPDATE) {
    await analytics.trackUserAction(
      'update',
      'extension',
      details.previousVersion ?? '6.1.0',
    );
    await openExtensionUpdateWelcomePage(details.previousVersion ?? '6.1.0');
  }
});

chrome.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as PremiumAlarms;
  if (name === PremiumAlarms.ShowAdPremiumBanner) {
    const { hasPremium, status } = await getLocalStoragePremiumData([
      LocalStoragePremiumKey.HasPremium,
      LocalStoragePremiumKey.Status,
    ]);
    if (hasPremium && status === 'Paid') {
      await setLocalStoragePremiumData({ isAdBannerShown: false });
    } else {
      await setLocalStoragePremiumData({ isAdBannerShown: true });
    }
    await chrome.alarms.clear(name);
  }
});

chrome.runtime.onMessage.addListener(async (message: PremiumMessage) => {
  if (message.action === PremiumActions.CloseAdPremiumBanner) {
    await chrome.alarms.create(PremiumAlarms.ShowAdPremiumBanner, {
      periodInMinutes: 24 * 60,
    });
  }
});

chrome.storage.local.onChanged.addListener(
  async (changes: { hasPremium?: { newValue: boolean } }) => {
    if (changes.hasPremium) {
      await setLocalStoragePremiumData({
        isAdBannerShown: !changes.hasPremium.newValue,
      });
      if (changes.hasPremium.newValue) {
        await setLocalStorageData({ onboardingScreenStatus: 'done' });
      }
    }
  },
);

chrome.storage.local.onChanged.addListener(
  async (changes: { userId?: { newValue: string } }) => {
    if (
      changes.userId &&
      (changes.userId.newValue === undefined || changes.userId.newValue === '')
    ) {
      await setLocalStorageData({ onboardingScreenStatus: 'step-one' });
    }

    if (changes.userId && changes.userId.newValue) {
      await setUninstallUrl(changes.userId.newValue);
    }
  },
);

chrome.webRequest.onBeforeRedirect.addListener(
  async (details) => {
    if (
      details.redirectUrl.includes('google.com/sorry') &&
      details.url.includes('youtube.com/watch')
    ) {
      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );
      if (currentState === 'connected') {
        await redirectToVideo(details.url, details.tabId);
      }
    }
  },
  { urls: ['*://*.youtube.com/*'], types: ['main_frame'] },
  [],
);

async function init(): Promise<void> {
  try {
    const { isBrowserError } = await getLocalStorageData(
      LocalStorageKey.IsBrowserError,
    );
    if (isBrowserError) {
      await analytics.trackError('Browser error detected', 'initialization');
      void setLocalStorageData({
        currentState: 'disconnected',
        targetState: 'disconnected',
      });
    } else {
      await getSessionMetaApiData();

      const shouldAutostart = process.env.SHOULD_AUTOSTART === 'true';

      if (shouldAutostart) {
        await analytics.trackUserAction('autostart', 'proxy');
        await initProxy();
      } else {
        await analytics.trackUserAction('manual_start', 'proxy');
      }

      await setSessionStorageData({ isInitialized: true });
    }
  } catch (error) {
    console.error(error);
    await analytics.trackError(
      error instanceof Error ? error.message : 'Unknown error',
      'initialization',
    );
    await setLocalStorageData({ currentState: 'error' });
  }
}

proxyController.fetchProxyData = getPremiumProxyData;

const { isInitialized } = await getSessionStorageData(
  SessionStorageKey.IsInitialized,
);

if (!isInitialized) {
  await init();
}
