import { Actions } from '../../app/actions/shared';
import {
  type DirectVideoOpenMessage,
  type Message,
} from '../../app/messages/shared';
import { analytics } from '../analytics/ga';
import {
  getLocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/shared';

export function getWatchUrl(url: string): string {
  const urlObj = new URL(url);
  const videoId = urlObj.searchParams.get('v');
  return videoId ? `/watch?v=${videoId}` : url;
}

export function getVideoId(url: string): string | null {
  const urlObj = new URL(url);
  return urlObj.searchParams.get('v');
}

export async function getVideoTime(url: string): Promise<string | null> {
  const { savedTime } = await getLocalStorageData(LocalStorageKey.SavedTime);
  const urlObj = new URL(url);
  const videoId = getVideoId(url);

  if (videoId && savedTime?.[videoId]) {
    return savedTime[videoId].toString();
  }
  return urlObj.searchParams.get('t');
}

export async function redirectToVideo(
  url: string,
  tabId: number,
): Promise<void> {
  const videoId = getVideoId(url);
  if (videoId) {
    const watchUrl = getWatchUrl(url);
    const tabs = await chrome.tabs.query({
      active: true,
      currentWindow: true,
    });
    const activeTab = tabs.length > 0 ? tabs[0] : undefined;

    if (activeTab?.id === tabId) {
      await analytics.trackUserAction('video_redirect', 'youtube', videoId);

      chrome.runtime.onMessage.addListener(async function listener(
        message: Message,
        { tab },
      ) {
        if (message.action === Actions.VideoOpenerReady && tab?.id === tabId) {
          const time = await getVideoTime(url);

          await chrome.tabs.sendMessage<DirectVideoOpenMessage>(tabId, {
            action: Actions.DirectVideoOpen,
            payload: {
              watchUrl,
              videoId,
              time,
            },
          });
          chrome.runtime.onMessage.removeListener(listener);
        }
      });
      await chrome.tabs.update(tabId, {
        url: `https://www.youtube.com`,
      });
    }
  }
}
